export default {
	created() {
		this.getLocation()
	},
	data() {
		return {
			latitude: '',
			longitude:'',
			markers:[{
				latitude: this.latitude,
				longitude: this.latitude,
				iconPath:'../../static/wxcomponentimg/dw@2x.png',
				width: 40,
				height: 40
			}]
		}
	},
	methods: {
		bindMap() {
			uni.navigateTo({
				url: '../../pages/index/map'
			})
		},
		getLocation(){
			uni.getLocation({
			    type: 'wgs84',
			    success: res=> {
					// console.log(res)
					this.latitude = res.latitude
					this.longitude = res.longitude
				}
			})
		}
	},
}
